import "./styles.css";
import { useState } from "react";
export default function App() {
  const [val, setVal] = useState("");
  const [meaning, setMeaning] = useState("");
  let emojis = {
    "😊": "smiling",
    "😔 ": "sad",
    "🥰": "love",
    "😉": "Winking Face",
    "🍇 ": "Grapes",
    "🍌": " Banana",
    "🍞": " Bread",
    "🍕 ": " Pizza",
    "🌮": " Taco",
    "🦊": " Fox"
  };

  let arrEmoji = Object.keys(emojis);
  console.log(arrEmoji);
  let inputHandler = (e) => {
    console.log(e.target.value);
    const userInput = e.target.value;
    let meaning = emojis[userInput];
    if (meaning === undefined) {
      meaning = "not in our database";
    }
    setMeaning(meaning);
  };

  let clickHandler = (emoji) => {
    let meaning = emojis[emoji];
    setMeaning(meaning);
  };
  return (
    <div className="App">
      <div>
        <h1>Know your emotions</h1>
        <input onChange={inputHandler}></input>
        <h1>{meaning}</h1>
        <ul>
          {arrEmoji.map((emoji) => {
            return (
              <span
                className="span"
                key={emoji}
                onClick={() => clickHandler(emoji)}
              >
                {emoji}
              </span>
            );
          })}
        </ul>
      </div>
    </div>
  );
}
